import React, { useState } from "react";
import Slider from "react-slick";
import { projectData } from "../data";
import { IProject } from "../interface";
import ModalComponent from "./ModalComponent";
import ProjectContentComponent from "./ProjectContentComponent";

function ProjectItem({
  projectData,
  onClickHandle,
}: {
  projectData: IProject;
  onClickHandle: (data: IProject) => void;
}) {
  let { name, demo, type } = projectData;

  function onClick() {
    if (typeof onClickHandle == "function") onClickHandle(projectData);
  }

  return (
    <div className="project-item" onClick={onClick}>
      <img className="project-item__img" src={demo} alt="Image" />
      <div className="project-item__mask"></div>
      <div className="project-item__info">
        <i className="fa fa-info"></i>
      </div>
      <div className="project-item__desc">
        <span className="project-item__name">
          {name.length < 15 ? name : name.slice(0, 15) + "..."}
        </span>
        <div className="project-item__tag">{type}</div>
      </div>
    </div>
  );
}

function ProjectComponent() {
  let [dataModal, setDataModal] = useState<IProject | null>(null);
  const settings = {
    dots: true,
    infinite: false,
    arrows: false,
    speed: 500,
    slidesToShow: 5,
    slidesToScroll: 5,
    responsive: [
      {
        breakpoint: 1400,
        settings: {
          slidesToShow: 4,
          slidesToScroll: 4,
        },
      },
      {
        breakpoint: 1200,
        settings: {
          slidesToShow: 3,
          slidesToScroll: 3,
        },
      },
      {
        breakpoint: 992,
        settings: {
          slidesToShow: 2,
          slidesToScroll: 2,
        },
      },
      {
        breakpoint: 768,
        settings: {
          slidesToShow: 1,
          slidesToScroll: 1,
        },
      },
      {
        breakpoint: 576,
        settings: {
          slidesToShow: 1,
          slidesToScroll: 1,
        },
      },
    ],
  };

  function onClickItem(data: IProject) {
    setDataModal(data);
  }

  function callbackModalClose() {
    setDataModal(null);
  }
  // onClick={onClickItem}
  return (
    <div className="project">
      <h2 className="project__title animation-element">Projects</h2>
      <p className="project__desc animation-element">
        Here are some game projects, web pages, mobile apps I have worked on.
      </p>
      <Slider className="project__slider animation-element" {...settings}>
        {projectData.map((o, i) => (
          <ProjectItem projectData={o} onClickHandle={onClickItem} key={i} />
        ))}
      </Slider>
      {dataModal ? (
        <ModalComponent
          title={dataModal.name}
          closeCallback={callbackModalClose}
          children={<ProjectContentComponent data={dataModal} />}
        />
      ) : null}
    </div>
  );
}

export default ProjectComponent;
