import React, { useEffect } from "react";
import SocialComponent from "./component/SocialComponents";
import NavigatorComponent from "./component/NavigatorComponent";
import HomeContainer from "./container/HomeContainer";
import PortfolioContainer from "./container/PortfolioContainer";

function App() {
  function isInViewport(element: Element) {
    if (!element) return;
    let rect = element.getBoundingClientRect();
    return (
      rect.top >= 0 &&
      rect.left >= 0 &&
      rect.bottom <=
        (window.innerHeight || document.documentElement.clientHeight) &&
      rect.right <= (window.innerWidth || document.documentElement.clientWidth)
    );
  }

  useEffect(() => {
    let arrs = document.getElementsByClassName("animation-element");
    let elements: Element[] = [];
    let index = 0;

    for (let i = 0; i < arrs.length; i++) {
      elements.push(arrs[i]);
    }

    elements.map((element) =>
      new Promise((res) => {
        if (
          !isInViewport(element) ||
          element.className.includes("animation-element--start")
        )
          return;
        index++;

        let time = index * 80;

        element.setAttribute("style", `transition-delay: ${time}ms;`);
        element.setAttribute(
          "class",
          `${element.className} animation-element--start`
        );

        setTimeout(res, time);
      }).then(() => {
        if (element) element.setAttribute("style", `transition-delay: 0ms;`);
      })
    );
  });
  useEffect(() => {
    let arrs = document.getElementsByClassName("animation-element");
    let elements: Element[] = [];

    for (let i = 0; i < arrs.length; i++) {
      elements.push(arrs[i]);
    }

    function handleScrollEvent(event: Event) {
      let element = elements.find(
        (element) =>
          isInViewport(element) &&
          !element.className.includes("animation-element--start")
      );
      if (element) {
        new Promise((res) => {
          if (!element) return res(null);
          element.setAttribute("style", `transition-delay: ${500}ms;`);
          element.setAttribute(
            "class",
            `${element.className} animation-element--start`
          );
          setTimeout(res, 1000);
        }).then(() => {
          if (element) element.setAttribute("style", `transition-delay: 0ms;`);
        });
      }
    }

    window.addEventListener("scroll", handleScrollEvent);
    return () => {
      window.removeEventListener("scroll", handleScrollEvent);
    };
  });

  return (
    <div className="App">
      <NavigatorComponent />
      <SocialComponent />
      <HomeContainer />
      <PortfolioContainer />
    </div>
  );
}

export default App;
