import React, { useEffect, useRef, useState } from "react";
import ModalComponent from "./ModalComponent";
import ImageFullStack from "../image/full-stack.png";

function NavigatorComponent() {
  let navigator = useRef(null);
  let [activeNavigator, setActiveNavigator] = useState(false);
  let [showCertificate, setShowCertificate] = useState(false);

  useEffect(() => {
    const handleScroll = (event: Event) => {
      if (window.pageYOffset > 20 && !activeNavigator) setActiveNavigator(true);
      else if (window.pageYOffset < 20 && activeNavigator)
        setActiveNavigator(false);
    };
    window.addEventListener("scroll", handleScroll, { passive: true });

    return () => window.removeEventListener("scroll", handleScroll);
  });

  function onClickCertificate() {
    setShowCertificate(true);
  }

  function closeModalCallback() {
    setShowCertificate(false);
  }

  return (
    <nav
      className={`navigator ${activeNavigator ? "navigator--active" : ""}`}
      ref={navigator}
    >
      <a
        className="navigator__item navigator__item--header animation-element"
        href="./"
      >
        <h1>Van Khac Huy</h1>
      </a>
      <div
        className="navigator__item navigator__item--link underline-animation animation-element"
        onClick={onClickCertificate}
      >
        Certificate
      </div>
      <a
        className="navigator__item navigator__item--link btn btn--primary animation-element"
        href="/CV.pdf"
        target="blank"
      >
        Get CV
      </a>
      {showCertificate ? (
        <ModalComponent
          title="Certificate"
          closeCallback={closeModalCallback}
          children={
            <img
              src={ImageFullStack}
              alt="Full stack certificate"
              width="100%"
            />
          }
        />
      ) : null}
    </nav>
  );
}

export default NavigatorComponent;
