import React, { useEffect, useState } from "react";
import Mouse from "../image/mouse.png";
import { animateScroll as scroll } from "react-scroll";

function MouseComponent() {
  let [active, setActive] = useState(true);

  useEffect(() => {
    const handleScroll = (event: Event) => {
      if (window.pageYOffset > 20 && active) setActive(false);
      else if (window.pageYOffset < 20 && !active) setActive(true);
    };
    window.addEventListener("scroll", handleScroll, { passive: true });

    return () => window.removeEventListener("scroll", handleScroll);
  });

  function onClick() {
    if (!active) return;
    scroll.scrollToBottom();
  }

  return (
    <div className={`mouse ${active ? "mouse--active" : ""}`} onClick={onClick}>
      <img src={Mouse} alt="Mouse" width="16" />
      <i className="mouse__arrow fa fa-chevron-down"></i>
    </div>
  );
}

export default MouseComponent;
