import React from "react";
import { IProject } from "../interface";

function ProjectContentComponent({ data }: { data: IProject }) {
  let { date, demo, desc, name, skills, url, video } = data;

  return (
    <div className="project-content">
      <div className="project-content__demo">
        {video ? (
          <iframe
            width="100%"
            height="90%"
            frameBorder="0"
            allowFullScreen
            src={video}
          ></iframe>
        ) : (
          <img src={demo} alt={name} width="100%" />
        )}
      </div>
      <div className="project-content__content">
        <div className="project-content__section">
          <div className="project-content__title">
            Project <strong>Description</strong>
          </div>
          <p className="project-content__text">{desc}</p>
        </div>
        <div className="project-content__section">
          <div className="project-content__title">
            Project <strong>Detail</strong>
          </div>
          <div className="project-content__text">
            <ul>
              <li>
                <i className="fa fa-caret-right text--color-active" />
                <span className="project-content__head text--color-active text--bold">
                  Date:
                </span>
                {date}
              </li>
              <li>
                <i className="fa fa-caret-right text--color-active" />
                <span className="project-content__head text--color-active text--bold">
                  Skills:
                </span>
                <div className="project-content__skill">
                  {skills.map((o, i) => (
                    <div className="project-content__tag" key={i}>
                      {o}
                    </div>
                  ))}
                </div>
              </li>
              <li>
                <i className="fa fa-caret-right text--color-active" />
                <span className="project-content__head text--color-active text--bold">
                  URL:
                </span>
                {url}
              </li>
            </ul>
          </div>
          {url ? (
            <a className="project-content__btn" href={url} target="blank">
              GO TO URL
            </a>
          ) : null}
        </div>
      </div>
    </div>
  );
}

export default ProjectContentComponent;
