import React from "react";
import { skillData } from "../data";

function SkillComponent() {
  return (
    <div className="skill-list">
      <p className="skill-list__title animation-element">here is my skills:</p>
      <div className="skill-list__content">
        {skillData.map(({ title, skills }, i) => (
          <div className="skill-column" key={i}>
            <div className="skill-column__title animation-element">
              {title + ":"}
            </div>
            <div className="skill-column__content">
              {skills.map(({ title, color }, i) => (
                <div
                  className={`badge badge--${color} animation-element`}
                  key={i}
                >
                  <div className="badge__title">{title}</div>
                </div>
              ))}
            </div>
          </div>
        ))}
      </div>
    </div>
  );
}

export default SkillComponent;
